
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import Badge from 'vux/src/components/badge/index.vue'
import Card from 'vux/src/components/card/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

import MyCanlendar from 'c%/mycanlendar/mycanlendar.vue'
import common from '@/libs/common.mixin'
import { mapState } from 'vuex'
export default{
mixins: [common],
components: {
Card,
Grid,
Badge,
Flexbox,
GridItem,
FlexboxItem,
MyCanlendar
},
data () {
return {
events: []
}
},
mounted () {
this.getEvents()
},
methods: {
date_change (datetime) {
if(this.events.indexOf(datetime)>-1){
this.$router.push({path:'./schedules',query:{datetime:datetime}})
}
},
getEvents () {
this.$rest('course_arranges/get_course_day?pagesize=1000')
.get()
.success(data=>{
if(data.list.length>0){
data.list.forEach(d=>{
this.events.push(this.$filter('int_date')(d))
})
}
})
}
},
computed: {
...mapState({
home_menu: state => state.ui_config.home_menu
})
}
}



